// *a b o u t*
.about {
  position: relative;
  display: flex;
  padding-top: 44px;
  padding-bottom: 36px;

  &-wrapper {
    .size(8);
    .size-lg(7);
    .size-md(6);
    margin-left: 30px;

    .lg-block({
      margin-left: 15px;
    })
  }

  &__form-wrap {
    position: absolute;
    top: -50px;
    right: 15px;
    .size(4);
    .size-lg(5);
    .size-md(6);
    text-align: center;
    margin: 0 15px;
    padding: 42px 32px;
    background-color: @col_base;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    border-radius: 4px;

    .lg-block({
      padding: 40px 25px;
    })
  }

  &__title {
    .title();
    margin-bottom: 26px;

    .lg-block({
      margin-bottom: 20px;
    })
  }

  &__items {
    .xmd-block({
      text-align: left;
    });

    &-item {
      display: flex;
      margin-bottom: 20px;

      .lg-block({
        margin-bottom: 15px;
      })
    }
  }
}

.about .about-wrapper {
  .size-xmd(12);

  .xmd-block({
    padding-top: 380px;
    text-align: center;
  })
}

.about .about__form-wrap {
  .size-xmd(11);

  .xmd-block({
    padding: 32px 15px;
  })
}

.item__content {
  margin-left: 22px;

  .lg-block({
    margin-left: 15px;
  });

  &-title {
    .subtitle();
    margin-bottom: 13px;
  }
}

.form-wrap {

  &__title {
    .subtitle();
  }
}

// *p r i c e*
.price {
  padding: 45px 0;
  background-color: @col_font;
  text-align: center;

  &__title {
    .title();
  }

  &__content {
    .size(10);
    .size-lg(11);
    .size-sm(12);
    margin: auto;
    margin-top: 29px;
    margin-bottom: 35px;
    .content-box();
    text-align: left;

    &-title,
    &-item {
      display: flex;
      justify-content: space-between;
    }

    &-title {
      .font_RC_B();
      .f18();
      color: @col_base;
      padding: 20px 30px;
      background-color: @col_main;

      .sm-block({
        padding: 20px 15px;
      })
    }

    &-item {
      position: relative;
      padding: 22px 30px;
      .item-border();

      .sm-block({
        padding: 10px 15px;
      });

      &::after {
        position: absolute;
        content: '';
        top: 0;
        right: 25%;
        width: 1px;
        height: 100%;
        border-left: 1px solid @col_item;

        .sm-block({
          right: 28%;
        })
      }
    }

    &-item:nth-child(odd){
      background-color: @col_font;
    }
  }
}

.price .content-item__fault {
  .sm-block({
    width: 70%;
  })
}

.price .content-item__cost {
  .font_RC_B();
  width: 20%;
  padding-left: 30px;

  .sm-block({
    width: 30%;
    padding-left: 15px;
    text-align: right;
  })
}

// *r e v i e w s*
.reviews {
  .container();
  background-image: url('../images/back_review.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: @col_font;
  text-align: center;
  padding-top: 45px;
  padding-bottom: 75px;

  .sm-block({
    padding-top: 38px;
    padding-bottom: 45px;
  });

  &__title {
    .title();
    color: @col_base;
  }

  &__main {
    position: relative;
    .size(10);
    .size-sm(11);
    .size-xs(12);
    margin: auto;

    &-text {
      .f18();
      line-height: 25px;
      margin-top: 45px;
      margin-bottom: 25px;

      .sm-block({
        margin-top: 35px;
        .f16();
      })
    }

    &-author {
      text-align: right;
      .font_RC_B();
      .f20();

      .sm-block({
        .f18();
        text-align: center;
      })
    }

    &::before,
    &::after {
      position: absolute;
      content: url('../img/svg/icons/quote-w.svg');
      width: 23px;
      height: 20px;
    }

    &::before {
      top: -15px;
      left: -30px;
      transform: rotate(180deg);

      .xs-block({
        left: -10px;
      })
    }

    &::after {
      bottom: 20px;
      right: -30px;

      .sm-block({
        bottom: 30px;
      });

      .xs-block({
        right: -10px;
      })
    }
  }
}

.reviews .reviews__main {
  .xs-block({
    .size(12);
  })
}

/* пагинация */
.list-wrap {
  list-style: none;
  counter-reset: super-list;
}

.list-wrap__item:after {
  content: counter(super-list);
  counter-increment: super-list;
}
