// *b r a n d s*
.brands {
  padding-top: 44px;
  padding-bottom: 50px;

  .lg-block({
    padding-top: 35px;
    padding-bottom: 10px;
  });

  &__title {
    margin-left: 30px;
    .title();

    .sm-block({
      margin-left: 15px;
      text-align: center;
    })
  }

  &__items {
    margin-top: 30px;
    .row-flex();

    picture {
      .col();
      .size(1.5);
      .size-lg(2);
    }
  }
}

.brands .brands__items {
  .md-block({
    margin-top: 15px;
  });

  picture {
    .size-md(3);
    .size-sm(4);
  }
}

// *f a q*
.faq {
  background-color: @col_font;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 50px;

  &__title {
    .title();
    margin-bottom: 30px;
  }

  &__content {
    .size(11);
    .size-md(12);
    margin: auto;
    .content-box();
    text-align: left;
  }

  &__item {
    position: relative;
    padding: 28px 0;
    padding-left: 110px;
    padding-right: 20px;
    border: 1px solid @col_back;

    .lg-block({
      padding-left: 100px;
    });

    .sm-block({
      padding: 18px 0;
      padding-left: 80px;
    });

    &-title {
      .font_RC_B();
      .f20();
    }

    &-answer {
      margin-top: 25px;
      line-height: 23px;

      .sm-block({
        margin-top: 15px;
        .f14();
        line-height: 20px;
      })
    }

    span {
      position: absolute;
      top: 15px;
      left: 30px;
      text-transform: uppercase;
      .font_RBl_Bl();
      .f42();
      color: @col_value;
      line-height: 49px;

      .lg-block({
        top: 22px;
        .f32();
        line-height: 34px;
      });

      .sm-block({
        top: 17px;
        .f22();
        line-height: 26px;
      })
    }

    &:hover {
      cursor: pointer;
    }
  }
}

details[open] summary {
  color: @col_main;
}

// *r e q u e s t*
.request {
  text-align: center;
  padding-top: 35px;
  padding-bottom: 45px;

  &__title {
    .title();
    margin-bottom: 30px;
  }

  &__form {
    .size-sm(11);
    margin: auto;
  }
}