.header {
  .container();
  background-image: url('../images/back.jpg');
  background-position: center;
  background-repeat: no-repeat;
  color: @col_font;

  .md-block({
    background-position: top;
  });

  &__top {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    .font_RobC_b();
    z-index: 1;

    &::after {
      position: absolute;
      background-color: @col_black;
      opacity: .4;
    }

    &-logo,
    &-menu,
    &-contact {
      opacity: 1;
      z-index: 55;
    }

    &-logo {

      .lg-block({
        display: flex;
        order: 1;
      });

      .xmd-block({
        order: 0;
      });

      &_title-desc {
        .f13();
      }
    }

    &-menu {
      ul {
        display: flex;
        text-transform: uppercase;

        .xmd-block({
          display: none;
          position: absolute;
          width: 100%;
          text-align: center;
          .f16();
          top: 50px;
          right: 0;
          background-color: @col_bottom;
          padding: 20px 20px;
          border-radius: 5px;
          z-index: 22;
        })
      }

      li:not(:last-child) {
        margin-right: 30px;

        .xmd-block({
          margin-right: 0;
        })
      }

      li {
        &:hover {
          color: @col_check;
        }
        &:visited {
          color: @col_check;
        }

        .xmd-block({
          margin: 20px 0;
        })
      }

      .lg-block({
        order: 2;
        margin: auto;
        margin-top: 15px;
      });

      .xmd-block({
        order: 0;
      })
    }

    &-contact {
      display: flex;

      &_phone {
        .f15();
        margin-right: 20px;
      }

      &_phone,
      &_btn {
        &:hover {
          color: @col_check;
        }
      }

      .lg-block({
        order: 1;
      });

      .xmd-block({
        display: none;
        order: 0;
      })
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
    align-items: center;
    .font_RC_B();
    .f18();
    padding-top: 120px;
    padding-bottom: 115px;

    .lg-block({
      padding-top: 70px;
      padding-bottom: 120px;
    });

    &-title {
      text-align: center;
      text-transform: uppercase;
      .f48();
      margin-bottom: 20px;

      .xmd-block({
        .f38();
        margin-bottom: 40px;
      });

      .xs-block({
        .f32();
      });

      span {
        .f18();

        .xmd-block({
          .f16();
        })
      }
    }

    &-text {
      text-align: center;
      margin-bottom: 42px;

      .xmd-block({
        display: none;
      })
    }
  }
}

// бургер
.burger-btn{
  display: none;

  .xmd-block({
    position: relative;
    display: flex;
    margin-left: 30%;
    order: 2;
  });

  .xs-block({
    margin-left: 0;
  })
}
.burger-btn:hover{
  cursor: pointer;
  .icon{
    //fill: @col_b-red;
  }
}
.burger-btn:active .icon:active{
  //fill: @col_b-red;
}
// burger show
.view{
  display: block !important;
  transition: 3s;
}