.footer {



  &__info {

    ul {
      display: flex;
      justify-content: space-between;
      .subtitle();
      padding-top: 40px;
      padding-bottom: 30px;

      .md-block({
        flex-wrap: wrap;
        justify-content: center;
        padding-top: 30px;
        padding-bottom: 10px;
      });

      .xmd-block({
        flex-direction: column;
      })
    }

    li {
      .size(3);
      .size-md(6);

      .md-block({
        margin-bottom: 20px;
      })
    }

    &-title {
      color: @col_main;
      margin-bottom: 15px;
    }
  }

  &__copyright {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    background-color: @col_bottom;
    .f14();
    color: @col_check;

    .sm-block({
      flex-wrap: wrap;
      justify-content: center;
    });

    &-info {
      .sm-block({
        margin-right: 10px;
        margin-bottom: 10px;
        text-align: center;
      })
    }
  }
}

.footer .footer__info {
  li {
    .size-xmd(12);
  }
}