.form__checkbox {
  display: flex;
  justify-content: center;

  .sm-block({
    margin-top: 25px;
  });

  &:hover {
    cursor: pointer;
  }
}

.checkbox-wrapper {
  position: relative;
  display: flex;
  justify-content: center;

  .check-text {
    color: @col_check;

    .sm-block({
      .f14();
    });

    &:hover {
      cursor: pointer;
    }
  }
}

.check-input {
  appearance: none;
  position: absolute;
  z-index: -1;
  border: none;
}

.check-input:checked {
  background-color: @col_base;
}

.check-box {
  position: absolute;
  bottom: 50%;
  left: -25px;
  width: 11px;
  height: 11px;
  .box-check();
  transform: translateY(50%);
}

.check-input:hover + .check-box {
  cursor: pointer;
}

.check-input:checked + .check-box {
  background-image: url('../images/checkbox-icon.png');
  background-repeat: no-repeat;
  background-position: 40% 60%;
}

.check-input:focus + .check-box {
  box-shadow: 0 0 2px 2px var(--warning);
}

.check-input:disabled + .check-box {
  border: 2px solid #9b9b9b;
}

.check-input:disabled:checked + .check-box {
  background-color: #9b9b9b;
}