.header__main-btn {

  a {
    text-transform: uppercase;
    border: 2px solid @col_base;
    border-radius: 2px;
    .f14();
    padding: 13px 24px 11px 24px;

    &:hover,
    &:focus {
      border-color: @col_check;
      background-color: @col_base;
      color: @col_check;
    }

    &:focus {
      border-width: 4px;
      box-shadow: 0 10px 20px rgba(121, 121, 121, .3);
    }
  }
}

// main button
.button {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  .font_RC_B();
  .f13();
  color: @col_base;
  background: @col_main;
  box-shadow: 0 2px 2px rgba(0, 0, 0, .25);
  border-radius: 2px;
  padding: 12px 0;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 0 12px 32px -5px rgba(32,32,32,0.79);
  }
}

// button
.price__btn,
.faq__btn {
  width: 275px;
  margin: auto;

  .sm-block({
    width: 94%;
  })
}

.faq__btn {
  margin-top: 35px;
}

// btn + media
.request__form-btn {
  .size(4.1);
  .size-lg(4.2);
}

.request__form .request__form-btn {
  .size-md(4.4);
  .size-sm(12);
}