// about - form
.about__form {
  width: 100%;
  margin-top: 30px;

  fieldset {
    margin: 0 15px;

    .lg-block({
      margin: 0;
    })
  }
}

.form-input,
.request__form-message {
  width: 100%;
  .f14();
  color: @col_black;
  margin-bottom: 26px;
  padding: 12px 0 11px 14px;
  background: @col_font;
  border: 1px solid @col_grey;
  border-radius: 2px;
}

// request form
.request__form-wrap {
  display: flex;
  justify-content: space-between;

  .sm-block({
    flex-direction: column;
    align-items: center;
  });

  label {
    .size(4.1);
    .size-lg(4.2);
  }
}

.request__form .request__form-wrap{
  label {
    .size-md(4.4);
    .size-sm(12);
  }
}

.request__form-message {
  padding-top: 5px;
  padding-bottom: 0;
  height: 40px;
  .size(8.24);
  .size-lg(8.3);
}

.request__form .request__form-message {
  .size-md(8.45);
  .size-sm(12);

  .sm-block({
    height: 80px;
  })
}


