.footer__map {
  position: relative;
  overflow: hidden;
  padding-bottom: 30%;
  height: 0;

  .lg-block({
    padding-bottom: 40%;
  });

  .sm-block({
    padding-bottom: 56.25%;
  })
}
.footer__map iframe {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}