// slick
.slick-dotted.slick-slider {
  margin-bottom: 0;
}

// arrow's
.slick-prev,
.slick-next{
  top: 50%;
  padding: 16px;
  background-color: rgba(0, 0, 0, 0.047);
}
.slick-prev{
  left: 0;
  z-index: 1;
  border-radius: 0 6px 6px 0;
}
.slick-next{
  right: 0;
  border-radius: 6px 0 0 6px;
}

.slick-prev:before,
.slick-next:before {
  content: '';
  display: block;
  position: absolute;

  top: 50%;
  border: 1px solid @col_font;
  opacity: .5;
  width: 10px;
  height: 10px;
  transform: translateY(-50%) rotate(45deg);
}
.slick-prev::before{
  left: 15px;
  border-right: 0;
  border-top: 0;
}
.slick-next::before{
  right: 15px;
  border-left: 1px;
  border-bottom: 1px;
}

.slick-active{
  outline: none;
}

// dott's
.slick-dots{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: baseline;
  bottom: -55px;
  left: 0;
  width: 100%;

  .xmd-block({
    display: none;
  })
}
.slick-dots li{
  width: 22px;
  height: 19px;
  margin: 0 10px;
  .f16();
}
li.slick-active{
  width: 41px;
  height: 43px;
  .f36();
  line-height: 42px;
  margin: 0 15px;
}

.slick-dots li button:before {
  content: '';
}
.slick-dots li.slick-active button:before {
  content: '';
  opacity: 1;
}