.icon{
  fill: @col_main;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 50px;
  border: 1px solid @col_grey;
  border-radius: 50%;
}

.i-menu {
  fill: @col_font;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 20px;
}