*:after {
  box-sizing: inherit;
}
body {
  font-family: 'RobotoCondensed-Reg', Tahoma, sans-serif;
  font-size: 16px;
  color: inherit;
  background-color: #fff;
}
img {
  max-width: 100%;
  height: auto;
}
.icon {
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0;
}
.full-width-outside {
  position: relative;
}
.full-width-outside::after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  top: 0;
  left: -50vw;
  right: -50vw;
  margin-right: auto;
  margin-left: auto;
  z-index: -1;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.html-hidden {
  overflow: hidden;
}
.hidden {
  display: none;
}
.show {
  display: block !important;
}
.center {
  max-width: calc(1230px + 2 * 20px);
  margin: 0 auto;
  padding: 0 20px;
}
.center::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
.grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: auto;
}
@font-face {
  font-family: 'RobotoCondensed-Reg';
  src: url("../fonts/RobotoCondensed-Regular.woff2") format('woff2'), url("../fonts/RobotoCondensed-Regular.woff") format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'RobotoCondensed-Bold';
  src: url("../fonts/RobotoCondensed-Bold.woff2") format('woff2'), url("../fonts/RobotoCondensed-Bold.woff") format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto-Black';
  src: url("../fonts/Roboto-Black.woff2") format('woff2'), url("../fonts/Roboto-Black.woff") format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
