.footer__copyright {
  max-width: 1230px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}
@media screen and (max-width: 600px) {
  .header__top,
  .header__main,
  .about,
  .price,
  .reviews,
  .brands,
  .faq,
  .request,
  .footer__info,
