  }
}
.icon {
  fill: #8c0014;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 50px;
  border: 1px solid #e2dede;
  border-radius: 50%;
}
.i-menu {
  fill: #f8f6f6;
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 20px;
}
.header {
  max-width: 1900px;
  margin: 0 auto;
  background-image: url('../images/back.jpg');
  background-position: center;
  background-repeat: no-repeat;
  color: #f8f6f6;
}
@media screen and (max-width: 780px) {
  .header {
    background-position: top;
  }
}
.header__top {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: 'RobotoCondensed-Bold';
  font-size: 14px;
  z-index: 1;
}
.header__top::after {
  position: absolute;
  background-color: #000;
  opacity: 0.4;
}
.header__top-logo,
.header__top-menu,
.header__top-contact {
  opacity: 1;
  z-index: 55;
}
@media screen and (max-width: 960px) {
  .header__top-logo {
    display: flex;
    order: 1;
  }
}
@media screen and (max-width: 600px) {
  .header__top-logo {
    order: 0;
  }
}
.header__top-logo_title-desc {
  font-size: 13px;
}
.header__top-menu ul {
  display: flex;
  text-transform: uppercase;
}
@media screen and (max-width: 600px) {
  .header__top-menu ul {
    display: none;
    position: absolute;
    width: 100%;
    text-align: center;
    font-size: 16px;
    top: 50px;
    right: 0;
    background-color: #202020;
    padding: 20px 20px;
    border-radius: 5px;
    z-index: 22;
  }
}
.header__top-menu li:not(:last-child) {
  margin-right: 30px;
}
@media screen and (max-width: 600px) {
  .header__top-menu li:not(:last-child) {
    margin-right: 0;
  }
}
.header__top-menu li:hover {
  color: #797979;
}
.header__top-menu li:visited {
  color: #797979;
}
@media screen and (max-width: 600px) {
  .header__top-menu li {
    margin: 20px 0;
  }
}
@media screen and (max-width: 960px) {
  .header__top-menu {
    order: 2;
    margin: auto;
    margin-top: 15px;
  }
}
@media screen and (max-width: 600px) {
  .header__top-menu {
    order: 0;
  }
}
.header__top-contact {
  display: flex;
}
.header__top-contact_phone {
  font-size: 15px;
  margin-right: 20px;
}
.header__top-contact_phone:hover,
.header__top-contact_btn:hover {
  color: #797979;
}
@media screen and (max-width: 960px) {
  .header__top-contact {
    order: 1;
  }
}
@media screen and (max-width: 600px) {
  .header__top-contact {
    display: none;
    order: 0;
  }
}
.header__main {
  display: flex;
  flex-direction: column;
  align-items: center;
