// width
.container(){
  max-width: 1900px;
  margin: 0 auto;
}

// font
.font_RobC_b(){
  .font_RC_B();
  .f14();
}

// title
.title(){
  text-transform: uppercase;
  .font_RC_B();
  .f22();
  color: @col_black;
}

.subtitle(){
  .font_RC_B();
  .f20();
  color: @col_black;
}

// color
.background-top(){
  background-color: @col_black;
  opacity: .4;
}

// box
.content-box(){
  background: @col_base;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .3);
  border-radius: 4px;
}

.item-border(){
  background: @col_base;
  border: 1px solid @col_item;
}

.box-check(){
  background-color: @col_base;
  border: 1px solid @col_back;
  border-radius: 2px;
}